.upload-log-steps {
  margin: 10px 30px;
}
.steps-content {
  min-height: 200px;
  padding: 30px 30px;
}
.steps-content .rdw-editor-main {
  border-radius: 5px;
  border: 1px solid #d6d6d6;
}
.upload {
  position: relative;
}
.upload .process-bar {
  text-align: center;
  margin-bottom: 50px;
}
.upload .process-bar ul {
  list-style: none;
}
.upload .process-bar ul li {
  display: inline-block;
  margin-right: 200px;
  position: relative;
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease;
}
.upload .process-bar ul li:after {
  position: absolute;
  left: 55px;
  top: 20px;
  content: ' ';
  width: 0px;
  height: 10px;
  background: -webkit-repeating-linear-gradient(45deg, #1ea4ec, #1ea4ec 4px, #1f8bc5 4px, #1f8bc5 10px);
  background: -o-repeating-linear-gradient(45deg, #1ea4ec, #1ea4ec 4px, #1f8bc5 4px, #1f8bc5 10px);
  background: repeating-linear-gradient(45deg, #1ea4ec, #1ea4ec 4px, #1f8bc5 4px, #1f8bc5 10px);
  -webkit-transition: 0.5s ease-in;
  -o-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
}
.upload .process-bar ul li .through {
  position: absolute;
  left: 55px;
  top: 20px;
  content: ' ';
  width: 190px;
  height: 10px;
  background: #0b3e5a;
}
.upload .process-bar ul li:last-child {
  margin-right: 0px;
}
.upload .process-bar ul li:last-child:after {
  display: none;
}
.upload .process-bar ul li:last-child .through {
  display: none;
}
.upload .process-bar ul li a {
  display: inline-block;
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 0 10px;
  color: white;
  font-weight: 700;
  line-height: 50px;
  background: white;
  color: #a6cde2;
}
.upload .process-bar ul li a:hover {
  background: #34bfff;
  color: white;
}
.upload .process-bar ul li a:after {
  position: absolute;
  content: ' ';
  top: 50px;
  left: -25px;
  color: #023157;
  text-align: center;
  width: 100px;
  font-size: 16px;
  font-weight: 900;
}
.upload .process-bar ul li a:hover {
  background: #34bfff;
  color: white;
}
.upload .process-bar ul li a:hover:after {
  color: white;
}
.upload .process-bar ul li a#step-one:after {
  content: '';
}
.upload .process-bar ul li a#step-two:after {
  content: '';
}
.upload .process-bar ul li a#step-thr:after {
  content: '';
}
.upload .process-bar ul li.active:after {
  width: 190px;
}
.upload .process-bar ul li.active a {
  background: #34bfff;
  color: white;
}
.upload .process-card {
  margin: 30px 15px;
  position: relative;
  -webkit-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
  width: 100%;
  padding: 30px;
  border-radius: 10px;
  min-height: 300px;
  background: white;
  -webkit-box-shadow: 0px 1px 1px 1px black;
          box-shadow: 0px 1px 1px 1px black;
}
.upload .process-card .demo-wrapper {
  min-height: 250px;
}
.upload .process-card .demo-editor {
  border: 1px solid #999999;
  min-height: 240px;
}
.upload .process-card .demo-editor:hover {
  cursor: text;
}
.upload .process-card.active {
  -webkit-animation: progress-card-move 0.5s;
          animation: progress-card-move 0.5s;
}
@-webkit-keyframes progress-card-move {
  0% {
    opacity: 1;
    top: 0;
  }
  50% {
    opacity: 0;
    top: 25px;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}



