.log-container {
  position: relative;
  padding-bottom: 20px;
  width: 100%;
  overflow: hidden;
}
.log-container .log-list {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.log-container .log-list.log-list-close {
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}
.log-container .log-detail {
  display: inline-block;
  vertical-align: top;
  position: absolute;
  left: 102%;
  width: 100%;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.log-container .log-detail.log-detail-show {
  -webkit-transform: translateX(-98%);
      -ms-transform: translateX(-98%);
          transform: translateX(-98%);
}
.tmlog {
  width: 98%;
  margin-top: 20px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.tmlog:hover {
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
.tmlog:hover h2 {
  font-size: 28px;
  color: #1dB0B8;
}
.tmlog .log-tags-icon {
  padding: 20px;
  padding-top: 100px;
  display: inline-block;
  vertical-align: middle;
  width: 100px;
  height: 100px;
  margin-right: 20px;
}
.tmlog .log-label {
  display: inline-block;
  vertical-align: top;
  width: 80%;
  padding: 20px;
  border-radius: 20px;
  height: 300px;
  overflow: hidden;
}
.tmlog .log-label .log-time span {
  margin-right: 20px;
}
.tmlog .log-label .log-time span:nth-child(1) {
  color: gray;
}
.tmlog .log-label .log-time span:nth-child(2) {
  color: #1dB0B8;
}
.tmlog .log-label .log-time span:nth-child(3) {
  float: right;
  visibility: hidden;
}
.tmlog .log-label h2 {
  font-size: 26px;
  position: relative;
}
.tmlog .log-label p {
  font-size: 18px;
  text-indent: 2rem;
}
@media screen and (max-width: 1000px) {
  .tmlog {
    width: 100%;
  }
  .tmlog .log-tags-icon {
    display: none;
  }
  .tmlog .log-label {
    padding: 5%;
    width: 98%;
  }
  .tmlog .log-label .log-time span:nth-child(3) {
    visibility: visible;
  }
}
@-webkit-keyframes fx-overnay {
  0% {
    visibility: visible;
    height: 0%;
  }
  50% {
    background: #107360;
    color: white;
    height: 50%;
  }
  100% {
    background: #107360;
    color: #FFF;
    height: 100%;
  }
}
@keyframes fx-overnay {
  0% {
    visibility: visible;
    height: 0%;
  }
  50% {
    background: #107360;
    color: white;
    height: 50%;
  }
  100% {
    background: #107360;
    color: #FFF;
    height: 100%;
  }
}
.tabs {
  margin-top: 50px;
  font-size: 15px;
  padding: 0px;
  list-style: none;
  background: #fff;
  -webkit-box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  display: inline-block;
  border-radius: 50px;
  position: relative;
}
.tabs a {
  text-decoration: none;
  color: #777;
  text-transform: uppercase;
  padding: 10px 20px;
  display: inline-block;
  position: relative;
  z-index: 1;
  -webkit-transition-duration: 0.6s;
       -o-transition-duration: 0.6s;
          transition-duration: 0.6s;
}
.tabs a i {
  margin-right: 5px;
}
.tabs .selector {
  height: 100%;
  display: inline-block;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 1;
  border-radius: 50px;
  -webkit-transition-duration: 0.6s;
       -o-transition-duration: 0.6s;
          transition-duration: 0.6s;
  -webkit-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
       -o-transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
          transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  background: #05abe0;
  background: -webkit-linear-gradient(45deg, #05abe0 0%, #8200f4 100%);
  background: -o-linear-gradient(45deg, #05abe0 0%, #8200f4 100%);
  background: linear-gradient(45deg, #05abe0 0%, #8200f4 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#05abe0', endColorstr='#8200f4', GradientType=1);
}



